
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import header from '@/components/header';
import LingLoading from '@/components/attendance/LingLoading';
export default {
data() {
return {
titleContent: '与园长进行沟通',
mails: [],
firstMail: [],//第一条消息
userId: '',
userInfo: [],
sendContent: '',
mailId: 0,
disabledSend: false,
defaultAvatar: 'http://cdn-didano.oss-cn-shenzhen.aliyuncs.com/xiaonuo/wx/img/logo_2.jpg',
state: {
isShow: true,
message: '加载中',
state: 'loading'
},
positionY: {
startY: 0,
endY: 0,
scrollY: 0
}
}
},
watch: {
sendContent(val) {
if(!val) {
document.getElementsByTagName('textarea')[0].style.height = '2rem'
}
}
},
created() {

this.userInfo = JSON.parse(sessionStorage.getItem('CURRENT_USER'));
if (this.userInfo.roleType === PARENT) {
this.userId = this.userInfo.parentId;
} else {
this.userId = this.userInfo.staffId;
}

if (this.$route.query.mailId || this.mailId != 0) {
this.mailId = this.$route.query.mailId;
this.titleContent = this.$route.query.dialogueTitle;
this._initMailContent();
} else if (this.mailId == 0) {
this.getMailIdByUserId();
}
},
methods: {

_initMailContent() {
this.$http.post(`${WX_URL}base/mailbox/post/findReply_ByNoticeId/${this.mailId}`).then((response) => {
response = response.data;
if ( response.success ) {
this.state.isShow = false;
this.mails = response.data;
this.firstMail = this.mails.mai;

this.$nextTick( ()=> {
document.body.scrollTop = document.body.scrollHeight;
this.positionY.scrollY = document.body.scrollTop;
});
}
},(response) => {
this.state.state = 'error';
this.state.message = '网络错误';
});
},
handleFocus() {
setTimeout(function(){
document.body.scrollTop = document.body.scrollHeight;
},300);
},

sendMessage() {
if (this.sendContent) {
this.disabledSend = true;
}
if (this.disabledSend) {
this.$http.post(`${WX_URL}base/mailbox/post/write_mail`,
{
'content':this.sendContent,
'mailId':this.mailId,
'studentId':this.userInfo.studentId,
'userId':this.userId,
'userType':this.userInfo.roleType}
).then( (response) => {
this.disabledSend = false;
response = response.data;
if (response.success) {
this.mailId = response.data;
this.sendContent = '';
this._initMailContent();
}
});
}
},

deleteMail(mail) {
this.$http.post(`${WX_URL}base/mailbox/post/deleteMailByBoss/${mail.id}`).then( (response) => {
response = response.data;
if( response.success ) {
this._initMailContent();
}
})
},

getMailIdByUserId() {
this.$http.post(`${WX_URL}base/mailbox/post/parentGetMailId/${this.userId}`).then((response)=> {
response = response.data;
if (response.success) {
this.state.isShow = false;
if (response.data) {
this.mailId = response.data.mai.id;
if (this.mailId) {
this.mails = response.data;
this.firstMail = this.mails.mai;
}
}
}
},(response) => {
this.state.isShow = false;
});
},
touchStart(event) {
this.positionY.startY = event.targetTouches[0].pageY;
},
touchEnd(event) {
this.positionY.endY = event.changedTouches[0].pageY;
if( (this.positionY.endY - this.positionY.startY < 0) && (document.body.scrollTop === this.positionY.scrollY) ) {
this._initMailContent();
}
},
fetchData() {
this._initMailContent();
}
},
components: {
'v-header': header,
LingLoading,
XTextarea
}
}



